.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 24px;

  @media screen and (min-width: $bp-desktop) {
    display: none;
  }

  &__button {
    width: 12px;
    height: 12px;
    padding: 0;
    background-color: var(--text-color-primary);
    border-radius: 50%;

    &--active {
      background-color: var(--color-accent);
    }
  }
}
